import {connect} from 'react-redux';

import ListCours from './ListCours.jsx';
import { getAppClasse, getAppSpecialite } from '../../../redux/reducers/appReducer.js';
import { loadChapitres } from '../../../redux/actions/chapitresActions';

import {getListChapitres,isLoadingChapitres, getChapitrePageNumber, getChapitrePageTotal} from '../../../redux/reducers/chapitreReducer';

function mapStateToProps(state) {
    return {
        specialite : getAppSpecialite(state),
        classe : getAppClasse(state),
        chapitres : getListChapitres(state),
        loading : isLoadingChapitres(state),
        pageNumber : getChapitrePageNumber(state),
        pageTotal : getChapitrePageTotal(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadChapitres: function(codeClasse,codeSpecialite,pageNumber){
            dispatch(loadChapitres(codeClasse,codeSpecialite,pageNumber));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListCours);
