import React from 'react';
import Pagination from "react-js-pagination";
import { lienGestionChapitre } from '../../../navigation/cours';
import { toLink } from '../../../navigation';
import { CODE_EXAMENS } from '../../../routes/liens';
import {  getControleTitle } from '../../../utils/fonctions';
import EnseignementCard from '../../../commun/EnseignementCard';
import EnseignementList from '../../../layouts/EnseignementLayout/EnseignementList';

class ListControles extends React.Component  {

    constructor(props){
        super(props);
        this.name = '';
        this.isDevoir = true;
        if(this.props.type===CODE_EXAMENS){
            this.name = "d'examens ";
            this.isDevoir = false;
        }else{
            this.name = "de devoirs ";
        }
        
    }

    ajouterControles = ()=>{
        toLink(lienGestionChapitre());
    }

    changePage = (pageNumber)=>{
        const {classe,specialite} = this.props;
        const codeSpecialite = specialite ? specialite.code : null;
        this.props.loadControles(classe.code || '',codeSpecialite,this.props.type,pageNumber);
        window.scroll(0,0);
    }

    pageNumber(){
        return this.isDevoir ? this.props.devoirPageNumber : this.props.examenPageNumber;
    }

    
    pageTotal(){
        return this.isDevoir ? this.props.devoirPageTotal : this.props.examenPageTotal;
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        const {specialite,classe} = this.props;
        let title = "";
        if(!specialite){
            title = `Tous les sujets ${this.name} (${classe.abreviation})`;
        }else{
            title = `Sujets ${this.name} ${specialite.name} (${classe.abreviation})`;
        }
        let controles = this.isDevoir ? this.props.devoirs : this.props.examens;
        return(
            <EnseignementList title = {title}>
                <div className="row">
                    {controles.map(({id,year,programme,exercises_count},i)=>(
                        <div key={id} className="col-12">
                            <EnseignementCard                                
                                title = {getControleTitle(this.props.classe.code,this.props.classe.type,this.props.type,year,i+1)} 
                                resume = {"Notions abordes : Thoreme de thales,"} 
                                exercice = {`${exercises_count} Exercice(s)`}
                                programme = {programme}
                                enseignementID={id}
                                type = {this.props.type}
                                icon
                            />
                        </div>                  
                    ))}
                </div>
                    <Pagination
                        activePage={this.pageNumber()}
                        itemsCountPerPage={5}
                        totalItemsCount={this.pageTotal()}
                        pageRangeDisplayed={5}
                        onChange={this.changePage}
                        hideNavigation={true}
                        hideFirstLastPages={true}
                        innerClass = "pagination pagination-lg justify-content-center"
                        itemClass = "page-item"
                        linkClass = "page-link"
                    />
            </EnseignementList>
        )
    }

}


export default ListControles;