import {connect} from 'react-redux';

//import {withRouter} from 'react-router-dom';

import {getAppClasse, getAppClasses, getAppPage, getAppSpecialite} from '../../redux/reducers/appReducer';
import { loadAppClasse, setAppPage, setAppSpecialite } from '../../redux/actions/appActions';

import EnseignementLayout from './EnseignementLayout.jsx';
import { CODE_COURS, CODE_DEVOIRS, CODE_EXAMENS } from '../../routes/liens';
import { loadChapitres } from '../../redux/actions/chapitresActions';
import { loadControles } from '../../redux/actions/controlesActions';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        classes : getAppClasses(state),
        specialite : getAppSpecialite(state),
        tab : getAppPage(state),
    };
}

function mapDispatchToProps(dispatch, getState) {
    return {
        loadAppClasse : function(codeClasse){
            dispatch(loadAppClasse(codeClasse));
        },
        setAppSpecialite : function(codeSpecialite){
            dispatch(setAppSpecialite(codeSpecialite))
        },
        setAppPage : function(page){
            dispatch(setAppPage(page));
        },
        loadEnseignements : function(codeClasse,codeSpecialite,tab){
            if(tab===CODE_COURS){
                dispatch(loadChapitres(codeClasse,codeSpecialite));
            }else if(tab===CODE_DEVOIRS || tab===CODE_EXAMENS){
                dispatch(loadControles(codeClasse,codeSpecialite,tab));
            }
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EnseignementLayout);
