import React from 'react';
//import {Link} from 'react-router-dom';
import classnames from 'classnames';
import TextToLatex from '../../../commun/TextRender';
//import { lienModifierEnonce, lienModifierCorrection } from '../../../navigation/cours';
//import ProtectedButton from '../../../commun/ProtectedButton';

export const ENONCE = 'e';
export const CORRECTION = 'c';

class ExerciceItem extends React.Component  {

    
    constructor(props){
        super(props);
        this.state = {
            page : ENONCE
        }
    }

    toggle(page){
        this.setState({page})
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        return (
            <div className="exercice">

                <div className="enseignement-menu">
                    <div className="enseignement-tab">
                        <div onClick={this.toggle.bind(this,ENONCE)}  className={classnames("enseignement-tab__item",{active : this.state.page===ENONCE})}>
                            <img alt="" className="enseignement-tab__icon" src={require('../../../img/icon-exercice.png')} />
                            <span className="enseignement-tab__name">Exercice {this.props.num}</span>
                        </div>
                        <div onClick={this.toggle.bind(this,CORRECTION)}  className={classnames("enseignement-tab__item",{active : this.state.page===CORRECTION})}>
                            <img alt="" className="enseignement-tab__icon" src={require('../../../img/icon-correction.png')} />
                            <span className="enseignement-tab__name">Correction</span>
                        </div>
                        <div className="enseignement-tab__item">
                            <img alt="" className="enseignement-tab__icon" src={require('../../../img/edit.png')} />
                        </div>
                        <div className="enseignement-tab__item">
                            <img alt="" className="enseignement-tab__icon" src={require('../../../img/delete.png')} />
                        </div>
                    </div>

                </div>
                
                <div className="content">
                    {this.state.page===ENONCE 
                        ? <TextToLatex text = {this.props.exercice.enonce} /> 
                        : <TextToLatex text = {this.props.exercice.correction} />
                    }
                </div>
            </div>  
        );
    }

}


export default ExerciceItem;