import React from 'react';
import Line from './Line';

const LoadingExercice = () =>(
    <div className="exercice">
        <div className="tabbar text-left bg-transparent">
            <div className="tabbar-item icon-left icon-exercice">Exercice {'-'}</div>
            <div className="tabbar-item icon-left icon-correction">correction</div>
        </div>
        <div className="content">
            {new Array(8).fill(1).map((_,i)=>(<Line key={"lineExo"+i} />))}
        </div>
    </div>
);

export default LoadingExercice;