import {connect} from 'react-redux';

import {getAppClasse, getAppSpecialite, getAppPage} from '../../../redux/reducers/appReducer';
import NavSpecialite from './NavSpecialite.jsx';


function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state),
        page : getAppPage(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {}
}

export default connect(mapStateToProps, mapDispatchToProps)(NavSpecialite);
