import React from 'react';
import {CODE_EXAMENS, CODE_COURS } from '../../routes/liens';
import EnseignementCard from '../../commun/EnseignementCard';
import EnseignementTitle from '../../commun/EnseignementTitle';
import LoadingRecent from '../../commun/PlaceHolder/LoadingRecent';
import { getControleTitle } from '../../utils/fonctions';
import PresentationApp from './PresentationApp';



const EnseignementsRecents = (props)=>{

    const {elements,loading, type,title} = props;

    return(
        <div className="enseignements-recents__item" >
            
            <EnseignementTitle 
                title={title} 
                icon={type} />
            
            <div className="row">
                {elements.map(({programme,id,title,year})=>{
                        let titre = "";
                        if(type===CODE_COURS){
                            titre = title
                        }else{
                            titre = getControleTitle(programme.classe.code, programme.classe.type, type, year)
                        }
                        return(
                            <div key={type+id} className="col-md-6 col-12">
                                <EnseignementCard
                                    title = {title}
                                    type = {type}
                                    programme = {programme}
                                    enseignementID = {id}
                                    icon
                                />
                            </div>
                        )
                    })
                }
            </div>
        </div>
    )
}


class Home extends React.Component  {


    componentDidMount(){
        this.props.loadChapitresRecents();
        //this.props.loadExamensRecents();
    }
    

        
    render(){
        return (
            <div className="home">
                <PresentationApp />
                <div className="container-fluid" style={{height:'100vh',backgroundColor:'#fff'}}>
                    <div className="container enseignements-recents"  >
                        <EnseignementsRecents
                            loading = {this.props.loadingChapitres}
                            type = {CODE_COURS}
                            title="Derniers cours"
                        elements = {this.props.chapitresRecents} />

                        <EnseignementsRecents
                            loading = {this.props.loadingExamens}
                            type = {CODE_EXAMENS}
                            title="Derniers sujets d'examens"
                            elements = {this.props.examensRecents} />
                    </div>
                </div>

            </div>
            
        );
    }

}

export default Home;