import React from 'react';
import { CODE_COURS, CODE_DEVOIRS, CODE_EXAMENS } from '../routes/liens';

const EnseignementTitle = (props)=>{
    let icon = null;
    if(props.icon === CODE_COURS){
        icon = "cours-icon.png";
    }else if(props.icon===CODE_DEVOIRS){
        icon = "devoirs-icon.png";
    }else if(props.icon===CODE_EXAMENS){
        icon = "examens-icon.png";
    }
    return(
        <div className="enseignement-title">
            {icon && <img src={require('../img/'+icon)} alt="" />}
            <span>{props.title}</span>
        </div>
    );
}

export default EnseignementTitle;