import {
    getChapitres, getChapitre, getExercices
} from '../../services/chapitreService';
import { getListChapitres, getChapitrePageNumber } from '../reducers/chapitreReducer';
import { setProgramme } from './appActions';
import { setLoadingExercices, setChapitreExercices, setChapitreExoCurPage, setChapitreExoTotal } from './exercicesActions';

import { toRoman } from '../../utils/toroman';
import marked from 'marked-katex';


export function loadExercices(chapitreID,page=null){
    return function(dispatch){
        dispatch(setLoadingExercices());
        getExercices(chapitreID,page).then(({data,meta})=>{
            dispatch(setChapitreExercices(data));
            dispatch(setChapitreExoTotal(meta.total));
            dispatch(setChapitreExoCurPage(meta.current_page));
            dispatch(setLoadingExercices(false));
        })
        .catch((e)=>{
            console.log(e);
            dispatch(setLoadingExercices(false));
        })
    }
}

/**
 * 
 */
export function loadChapitres(codeClasse=null,codeSpecialite=null,pageNumber){
    return function(dispatch,getstate){
        if(!codeClasse){
            return;
        }
        dispatch(setLoadingChapitres());
        if(!pageNumber){
            pageNumber = getChapitrePageNumber(getstate());
        }
        getChapitres(codeClasse,codeSpecialite,pageNumber).then(({data,meta})=>{
            dispatch(setChapitres(data));
            dispatch(setChapitrePageNumber(pageNumber));
            dispatch(setChapitrePageTotal(meta.total));
            dispatch(setLoadingChapitres(false));
            console.log('CHAPITRES LOADED => '+codeClasse + ' '+codeSpecialite + ' '+ pageNumber)
        })
        .catch((error)=>{
            dispatch(setLoadingChapitres(false));
            console.log(error);
        })
    }
}

/**
 * 
 */
export function loadChapitre(chapitreID){
    return function(dispatch,getState){
        dispatch(setLoadingChapitre());
        getChapitre(chapitreID).then(({data})=>{
            const {contents, contentHtml} = getTableMatieres(data.content);
            data.contents = contents;
            data.content = contentHtml;
            dispatch(setChapitre(data));
            dispatch(setLoadingChapitre(false));
            if(getListChapitres(getState()).length === 0){
                const {classe,specialite} = data.programme;
                dispatch(loadChapitres(classe.code,specialite.code));
            }
            dispatch(setProgramme(data.programme));
        })
        .catch((error)=>{
            dispatch(setLoadingChapitre(false));
            console.log(error);
        })
    }
}

function getTableMatieres(contenu){
    if(contenu===null){
        return [];
    }
    let contents = [];
    let contentHtml = document.createElement("div");
    
    contentHtml.innerHTML = marked(contenu);
    let hs = contentHtml.querySelectorAll("h1, h2");

    let index = -1;
    let subIndex = 1;
    for(let i=0; i < hs.length; i++){
        let numero = null;
        if(hs[i].tagName==="H1"){
            index = index + 1;
            numero = toRoman(index + 1);
            contents.push({
                id : hs[i].id,
                titre : numero+". "+hs[i].innerText,
                subSections : []
            });
            subIndex = 1;
        }else{
            numero = toRoman(index + 1)+"."+subIndex;
            contents[index] && contents[index].subSections.push({
                titre : numero+" "+hs[i].innerText,
                id : hs[i].getAttribute("id")
            });
            subIndex++;
        }
        hs[i].setAttribute("data-numero",  numero);
    }
    return {contents,contentHtml: contentHtml.innerHTML};
}

//
function setChapitre(chapitre={}){
    return {type : "SET_CHAPITRE", chapitre};
}
function setLoadingChapitre(loading=true){
    return {type : "LOADING_CHAPITRE", loading};
}

//
function setLoadingChapitres(loading=true){
    return {type : "LOADING_CHAPITRES", loading};
}
function setChapitres(chapitres=[]){
    return {type : "SET_CHAPITRES",chapitres};
}
function setChapitrePageNumber(pageNumber=0){
    return {type : "SET_CHAPITRE_PAGE_NUMBER",pageNumber};
}
function setChapitrePageTotal(pageTotal=0){
    return {type : "SET_CHAPITRE_PAGE_TOTAL",pageTotal};
}


// Edition chapitre
export function toggleChapitreModal(){
    return {type : "TOGGLE_CHAPITRE_MODAL"};
}