import { getExamensRecents, getChapitresRecents } from "../../services/recentService";

export function loadExamensRecents(){
    return function(dispatch){
        dispatch(setLoadingExamens());
        getExamensRecents().then(({data})=>{
            console.log(data);
            dispatch(setLoadingExamens(false));
            dispatch(setExamensRecents(data));
        })
        .catch((error)=>{
            dispatch(setLoadingExamens(false));
            console.log(error);
        })
    }
}

export function loadChapitresRecents(){
    return function(dispatch){
        dispatch(setLoadingChapitres());
        getChapitresRecents().then(({data})=>{
            console.log(data);
            dispatch(setLoadingChapitres(false));
            dispatch(setChapitresRecents(data));
        })
        .catch((error)=>{
            dispatch(setLoadingChapitres(false));
            console.log(error);
        })
    }
}

//
function setLoadingExamens(loading=true){
    return {type : "LOADING_EXAMENS_RECENTS", loading};
}
function setLoadingChapitres(loading=true){
    return {type : "LOADING_CHAPITRES_RECENTS", loading};
}
function setChapitresRecents(data=[]){
    return {type : "SET_CHAPITRES_RECENTS", data};
}
function setExamensRecents(data=[]){
    return {type : "SET_EXAMENS_RECENTS", data};
}