import {connect} from 'react-redux';

import ListControles from './ListControles.jsx';
import { getAppClasse, getAppSpecialite } from '../../../redux/reducers/appReducer.js';
import {loadControles} from  '../../../redux/actions/controlesActions';
import { getDevoirs, getExamens, getDevoirPageTotal, getDevoirPageNumber, getExamenPageNumber, getExamenPageTotal } from '../../../redux/reducers/controleReducer.js';

function mapStateToProps(state) {
    return {
        classe : getAppClasse(state),
        specialite : getAppSpecialite(state),
        devoirs : getDevoirs(state),
        examens : getExamens(state),
        //isLoadingChapitres : isLoadingChapitres(state)
        
        devoirPageNumber : getDevoirPageNumber(state),
        devoirPageTotal : getDevoirPageTotal(state),

        examenPageNumber : getExamenPageNumber(state),
        examenPageTotal : getExamenPageTotal(state)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        loadControles: function(codeSpecialite,codeClasse,type,pageNumber){
            dispatch(loadControles(codeSpecialite,codeClasse,type,pageNumber));
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ListControles);
