import React from 'react';
import classnames from 'classnames';
import SpecialiteDropdown from '../../ui/Header/SpecialiteDropdown';
import { ENSEIGNEMENTS } from './EnseignementLayout.jsx';
import { CODE_EXAMENS } from '../../routes/liens';
import { diseable, isClasseExamen } from '../../utils/fonctions';



class EnseignementMenu extends React.Component {
    
    render() {

        const listEnseignements = [];
        for (var code in ENSEIGNEMENTS) {
            if (ENSEIGNEMENTS.hasOwnProperty(code)) {
                const classOptions = {
                    active: code===this.props.page
                }
                if(CODE_EXAMENS===code){
                    classOptions.diseable = !isClasseExamen(this.props.classe.type)
                }
                listEnseignements.push(
                    <div onClick = {this.props.changeEnseignement.bind(this,code)} key={code} 
                        className={classnames("enseignement-tab__item",classOptions)}>
                        <img alt="" className="enseignement-tab__icon" src={ENSEIGNEMENTS[code].image} />
                        <span className="enseignement-tab__name">{ENSEIGNEMENTS[code].name}</span>
                    </div>
                )
            }
        }

        return (
            <div className="bg-blue-light">
                <div className="container">
                    <div className="enseignement-menu">
                        <div className="enseignement-specialite">
                            <SpecialiteDropdown 
                                classe = {this.props.classe}
                                specialite = {this.props.specialite}
                                page = {this.props.page}
                            />
                        </div>
                        <div className="enseignement-tab">
                            {listEnseignements}
                        </div>
                    </div>
                </div>
            </div>
        )
    }


}

// Exporte le composant
export default EnseignementMenu;