import React from 'react';
import qs from 'qs';
import TimeLine from '../../../commun/TimeLine';
import { CODE_DEVOIRS, CODE_EXAMENS } from '../../../routes/liens';
import Loading from '../../../commun/Loading';
import Presentation from '../../../commun/Presentation';
import { getControleTitle } from '../../../utils/fonctions';
import ListExercice from '../../Exercices/ListExercice';
import { toLink, getLienEnseignement } from '../../../navigation';
import { lienOuvrirControle } from '../../../navigation/controles';
import MenuRetour from '../../../commun/Retour';



class VoirControle extends React.Component  {

    constructor(props){
        super(props);
        this.state = {
            page : 1
        }
    }

    componentDidMount(){
        window.scroll(0,0);
        let {exo} = qs.parse(this.props.location.search,{ ignoreQueryPrefix: true });
        if(exo){
            exo = parseInt(exo);
            this.setState({page:  exo ? exo : 1});
        }
        this.props.loadControle(this.props.match.params.controleID);
    }

    editerControle = ()=>{

    }

    isDevoir(){
        return this.props.controle.type==='devoir';
    }

    makeTitle(typeControle){
        if(this.isDevoir()){
            return "Sujet de devoirs";
        }
        const {code,type} = this.props.classe;
        return getControleTitle(code,type,typeControle,this.props.controle.year);
    }

    loadExercice = (page)=>{
        this.setState({page});
        toLink(`${lienOuvrirControle(this.props.controle.id)}?exo=${page}`);
    }

    render(){
        
        if(!this.props.controle.id){
            return <Loading />
        }
        let code = '';

        if(this.isDevoir()){
            code = CODE_DEVOIRS;
        }else{
            code = CODE_EXAMENS;
        }
        const {programme,exercises} = this.props.controle;
        const listExercices = exercises[this.state.page-1] ? [exercises[this.state.page-1]] : [];
        return (
            <div className="enseignement-container">
                <MenuRetour
                    title = {this.makeTitle(code)} 
                    link = {getLienEnseignement({codeClasse : programme.classe.code, codeSpecialite : programme.specialite.code, page : code})}
                />
                <div className="enseignement-container__main">
                    <div className="container">
                        <Presentation
                            className="controles"
                            resume = {"Notions abordes : thoreme de thales"}
                            editer = {this.editerControle}
                            programme = {programme}
                        />

                    <div className="enseignement-content">
                        <ListExercice
                            exercices = {listExercices}
                            page = {this.state.page}
                            total = {exercises.length}
                            loading = {false}
                            loadExercice = {this.loadExercice}
                        />       
                    </div>
                    </div>
                </div>
            </div>
        );
    }

}


export default VoirControle;