import React from 'react'
import { Switch, Route } from 'react-router-dom'
import VoirControle from '../pages/Controles/Lecture';



const ControleLayout = (props) => {
    
    return(
        <>
            <Switch>
                <Route exact path={`${props.match.url}/lecture/:controleID`} component={VoirControle} />
                {/*<Route excat path={`${props.match.url}/gestion/:controleID`} component={EditerResume} />
                <Route excat path={`${props.match.url}/gestion`} component={EditerResume} />*/}
            </Switch>
        </>
    );
}

export default ControleLayout