import React from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import { toLienEnseignement } from '../../navigation';

class SpecialiteDropdown extends React.Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
            dropdownOpen: false
        };
    }

    toggle() {
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    setSpecialite = (codeSpecialite)=>{
        toLienEnseignement({codeClasse : this.props.classe.code, codeSpecialite, page : this.props.page});
    }

    render() {
        const {specialite,classe} = this.props;
        const hasSpecialite = specialite && specialite.code;
        const specialites = hasSpecialite ? classe.specialites.filter((s)=>(s.code!==specialite.code)) : classe.specialites;
        return (
            <ButtonDropdown isOpen={this.state.dropdownOpen} toggle={this.toggle} className="enseignement-specialite__btn">
                <DropdownToggle color="outline-secondary" className="btn-block shadow-sm" caret>
                    {hasSpecialite ? specialite.name : "Matires"}
                </DropdownToggle>
                {specialites.length !== 0 && 
                    <DropdownMenu>
                        {specialites.map((specialite)=>(
                            <DropdownItem  onClick={this.setSpecialite.bind(this,specialite.code)} 
                                key={specialite.code}
                            >
                                {specialite.name}
                            </DropdownItem>
                        ))}
                    </DropdownMenu>
                }
            </ButtonDropdown>
        );
    }
}

export default SpecialiteDropdown;