import React from 'react';

const PresentationApp = (props)=>(
    <div className="container-fluid plateforme">
        <div className="jumbotron">
            <div className="description-container text-center">
                <h1 className="description-titre">Plateforme de partage de connaisances</h1>
                <p className="description-text">
                    Retrouver sur lonnya des cours, des sujets de devoirs, de BEPC, de BAC et des livres
                </p>
                <img src={require('../../img/icon-home-maths.png')} />
                <img src={require('../../img/icon-home-biologie.png')} />
                <img src={require('../../img/icon-home-pc.png')} />
            </div>
            <img className="teacher-icon" src={require('../../img/teacher-icon.png')} alt=""/>
        </div>
    </div>
);

export default PresentationApp;