import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import { getLienEnseignement, toLienEnseignement } from '../../navigation';
import { HOME, CODE_EXAMENS } from '../../routes/liens';
import { isClasseExamen } from '../../utils/fonctions';


class Authentification extends React.Component{
    
    isConnect(){
        return this.props.user && this.props.user.token;
    }

    toggleAuth = ()=>{
        this.props.toggleAuth();
    }

    btnLogin(){
        return(
            <div className="header-auth">
                <div onClick={this.toggleAuth} className="btn btn-outline-gris login-btn">Se connecter</div>
                <div className="btn btn-rouge ml-2 shadow-sm">S'inscrire</div>
            </div>
        )
    }

    renderProfil(){
        return(
            <React.Fragment>
                <div className="profil" onClick={this.props.logout} data-name={this.props.user.firstname[0] + this.props.user.lastname[0]}>
                </div>

            </React.Fragment>
        )
    }

    render(){
        return(
            <React.Fragment>
                <div className="clearfix">
                    {this.isConnect() ? this.renderProfil() : this.btnLogin()}
                </div>
                <div className="header-icon">
                    <img src={require('../../img/menu.png')}  alt="logo"/>
                </div>
            </React.Fragment>
        )
    }
}


class AppHeader extends React.Component{

    openClasse(codeClasse){
        let classe = this.props.classes.find(item=>item.code===codeClasse);
        if(classe){
            const {page,specialite} = this.props;
            let canOpenSpecialite = false;
            if(specialite && specialite.code){
                canOpenSpecialite = classe.specialites.findIndex(s=>s.code===specialite.code) !== -1;
            }
            if(page===CODE_EXAMENS && !isClasseExamen(classe.type)){
                alert("KO");
                return;
            }
            toLienEnseignement({
                codeClasse, 
                page : page, 
                codeSpecialite: canOpenSpecialite===true ? specialite.code : null});
        }
    }

    render(){

        const {classe} = this.props;
    
        return(
            <div className="bg-secondary">
    
                <div className="container">
                
                    <header className="header">
                        
                        <div className="left">
                            <Link to={HOME} className="logo">
                                <img className="shadow-sm" src={require('../../img/logo.png')} alt="Lonnya" />
                            </Link>
                            
                            <div className="nav">
                                {this.props.classes.map(({code,name})=>(
                                    <div key={code} className={classnames("nav-item",{active : classe && classe.code === code})} 
                                        onClick = {this.openClasse.bind(this,code)}>
                                        {name} 
                                    </div>
                                ))}
                            </div>
                        </div>
                            {/*<InputSearch />*/}
                        <div className="right">
                            
                            <Authentification
                                user = {this.props.user}
                                logout = {this.props.logout}
                                toggleAuth = {this.props.toggleAuth}
                            />
                        </div>
                    </header>
                </div>
            </div>
        );
    }
    
}



export default AppHeader;