import { CODE_DEVOIRS } from "../../routes/liens";
import { getControles, getControle } from "../../services/controleService";
import {  setProgramme } from "./appActions";

export function loadControles(codeClasse='',codeSpecialite='',type=CODE_DEVOIRS,pageNumber=1){
    return function(dispatch){
        dispatch(setLoadingControles());
        getControles(codeClasse, codeSpecialite, type, pageNumber).then(({data,meta})=>{
            let isDevoir = type===CODE_DEVOIRS;
            if(isDevoir){
                dispatch(setDevoirs(data));
            }else{
                dispatch(setExamens(data));
            }
            dispatch(setControlePageNumber(pageNumber,isDevoir));
            dispatch(setControlePageTotal(meta.total,isDevoir));
            dispatch(setLoadingControles(false));
        })
        .catch((error)=>{
            dispatch(setLoadingControles(false));
            console.log(error);
        })
    }
}

/**
 * 
 */
export function loadControle(controleID){
    return function(dispatch){
        dispatch(setLoadingControle());
        getControle(controleID).then(({data})=>{
            dispatch(setControle(data));
            dispatch(setLoadingControle(false));
            dispatch(setProgramme(data.programme));
        })
        .catch((error)=>{
            dispatch(setLoadingControle(false));
            console.log(error);
        })
    }
}

//
function setControle(controle={}){
    return {type : "SET_CONTROLE", controle};
}
function setLoadingControle(loading=true){
    return {type : "LOADING_CONTROLE", loading};
}

//
function setLoadingControles(loading=true){
    return {type : "LOADING_CONTROLES", loading};
}
function setDevoirs(controles=[]){
    return {type : "SET_DEVOIRS",controles};
}
function setExamens(controles=[]){
    return {type : "SET_EXAMENS",controles};
}
function setControlePageNumber(pageNumber=0,isDevoir=true){
    return {type : "SET_CONTROLE_PAGE_NUMBER",data : {pageNumber,isDevoir}};
}
function setControlePageTotal(pageTotal=0,isDevoir=true){
    return {type : "SET_CONTROLE_PAGE_TOTAL",data : {pageTotal,isDevoir}};
}