import React from 'react';
import {Link} from 'react-router-dom';

import classnames from 'classnames';
import { getLienEnseignement } from '../../../navigation';

const ICON_SPECIALITES = {
    "pc" : require("../../../img/icon-pc.png"),
    "maths" : require("../../../img/icon-maths.png"),
    "biologie" : require("../../../img/icon-biologie.png")
}
const NavSpecialite = ({classe, specialite,page})=>(
    <div className="nav-specialite">
        {classe && classe.specialites.map((item,i)=>(
            <Link to={getLienEnseignement({codeClasse : classe.code, codeSpecialite : item.code, page})} 
                key={"ce"+i} 
                className={classnames("nav-specialite__item",{active : specialite && specialite.code===item.code})}
            >
                <img className="nav-specialite__icon" src={ICON_SPECIALITES[item.code]} alt=""/>
                <span className="nav-specialite__titre">{item.name}</span>
            </Link>
        ))}
    </div>
);

export default NavSpecialite;