import React from 'react';
import EnseignementTitle from '../../commun/EnseignementTitle';
import NavSpecialite from './NavSpecialite/NavSpecialite';


class EnseignementList extends React.Component {
    
    render() {
        return (
            <div style={{backgroundColor:'#fff', minHeight : '100vh'}} >
                <div className="container">
                    <div className="enseignement-list">
                        <div className="enseignement-list__container">
                            <div className="enseignement-list__content">
                                <div className="enseignement-list__header">
                                    <EnseignementTitle title = {this.props.title} />
                                    <hr />
                                </div>
                                {this.props.children}
                            </div>
                            <div className="enseignement-list__aside">
                                <NavSpecialite  />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}


// Exporte le composant
export default EnseignementList;