import React from 'react';
import Pagination from "react-js-pagination";
import { lienGestionChapitre } from '../../../navigation/cours';
import { toLink } from '../../../navigation';
//import Loading from '../../../commun/Loading';
import EnseignementCard from '../../../commun/EnseignementCard';
import { CODE_COURS } from '../../../routes/liens';
import EnseignementList from '../../../layouts/EnseignementLayout/EnseignementList';

class ListCours extends React.Component  {


    getSpecialite(){
        const specialite = this.props.classe.specialites.find((specialite)=>(
            specialite.code===this.props.match.params.codeSpecialite
        ));
        return specialite || {};
    }

    ajouterCours = ()=>{
        toLink(lienGestionChapitre());
    }

    changePage = (pageNumber)=>{
        const {classe,specialite} = this.props;
        const codeSpecialite = specialite ? specialite.code : null;
        this.props.loadChapitres(classe.code || '',codeSpecialite,pageNumber);
        window.scroll(0,0);
    }
    /**
     * Rendu de la page HTML
     */
    render(){
        const {specialite,classe} = this.props;
        let title = "";
        if(!specialite){
            title = `Tous les cours (${classe.abreviation})`;
        }else{
            title = `Cours de ${specialite.name} (${classe.abreviation})`;
        }
        return(
            <EnseignementList title = {title}>
                <div className="row">
                    {this.props.chapitres.map(({id,resume,title,programme,exercises_count})=>(
                        <div key={id} className="col-12">
                            <EnseignementCard
                                type = {CODE_COURS}
                                enseignementID = {id}
                                title = {title}
                                resume = {resume}
                                exercice = {`${exercises_count} Exercice(s)`}
                                programme = {programme}
                                icon
                            />
                        </div>                  
                    ))}
                </div>
                <div className="text-center">
                    <Pagination
                        activePage={this.props.pageNumber}
                        itemsCountPerPage={5}
                        totalItemsCount={this.props.pageTotal}
                        pageRangeDisplayed={5}
                        onChange={this.changePage}
                        hideNavigation={true}
                        hideFirstLastPages={true}
                        innerClass = "pagination mt-3 justify-content-center"
                        itemClass = "page-item"
                        linkClass = "page-link"
                        />
                </div>
            </EnseignementList>
        )
    }
}


export default ListCours;