import React from 'react';

import ExerciceItem from './ExerciceItem';
import classnames from 'classnames';
import ProtectedButton from '../../../commun/ProtectedButton';
import EnseignementAside from '../../Cours/VoirCours/Chapitre/EnseignementAside';
import LoadingExercice from '../../../commun/PlaceHolder/LoadingExercice';

class ListExercice extends React.Component  {

    loadExercice(newPage){
        if(newPage !== this.props.page){
            this.props.loadExercice(newPage);
        }
    }

    /**
     * Rendu de la page HTML
     */
    render(){
        const {exercices,total} = this.props;
        return (
            <>
                <div id="enseignement-content__main" className="enseignement-content__main">
                    <div className="exercices">
                        { this.props.loading
                            ? <LoadingExercice />
                            : 
                            this.props.exercices.map((exercice,i)=>(
                                <ExerciceItem 
                                    key={exercice.id} 
                                    num = {this.props.page} 
                                    exercice = {exercice} />
                            ))
                        }
                    </div>
                </div>
                {/*exercices && exercices.length !== 0 &&
                    <div id="enseignement-content__aside" className="enseignement-content__aside">
                        <EnseignementAside>
                            <ProtectedButton
                                onClick = {()=>{}}
                                label = "Ajouter exercice"
                                labelClass="text-success"
                                icon = "add"/>
                            <div className="nav-specialite">
                                {total !== 0 && (new Array(total)).fill(1).map((exercice,i)=>(
                                    <div key={"ce"+i}
                                        onClick={this.loadExercice.bind(this,i+1)}
                                        className={classnames("nav-specialite__item",{active : (i+1).toString()===this.props.page.toString()})}>
                                        
                                        <img className="nav-specialite__icon" src={require('../../../img/icon-exercice.png')} alt=""/>
                                        <span className="nav-specialite__titre">Exercice {i+1}</span>
                                    </div>
                                ))}
                            </div>
                        </EnseignementAside>
                    </div>
                            */}
            </>
        );
    }

}


export default ListExercice;