import React from 'react';
import { BASE, CODE_EXAMENS, CODE_COURS, CODE_DEVOIRS, CODE_LIVRES} from '../../routes/liens.js';
import { toLink } from '../../navigation/index.js';
import ListCours from '../../pages/Cours/ListCours/ListCours';
import EnseignementMenu from './EnseignementMenu';
//import EnseignementList from './EnseignementList.jsx';
import ListDevoirs from '../../pages/Controles/List/ListDevoirs.jsx';
import ListExamens from '../../pages/Controles/List/ListExamens.jsx';
import { diseable } from '../../utils/fonctions.js';

const _ENSEIGNEMENTS = {};

_ENSEIGNEMENTS[CODE_COURS] = {
    name : "Cours",
    image : require('../../img/cours-icon.png')
};
_ENSEIGNEMENTS[CODE_DEVOIRS] = {
    name : "Devoirs",
    image : require('../../img/devoirs-icon.png')
};
_ENSEIGNEMENTS[CODE_EXAMENS] ={
    name : "Examens",
    image : require('../../img/examens-icon.png')
};
_ENSEIGNEMENTS[CODE_LIVRES] ={
    name : "Livres",
    image : require('../../img/livres-icon.png')

};

export const ENSEIGNEMENTS = _ENSEIGNEMENTS;


class EnseignementLayout extends React.Component {
    
    constructor(props){
        super(props)
        
        // si la classe n'est defini => redirection
        const index = props.classes.findIndex(({code})=>(
            this.props.match.params.codeClasse===code
        ));
        if(index === -1){
            toLink(BASE);
        }
        const {codeClasse,codeSpecialite} = this.props.match.params;
        this.props.loadAppClasse(codeClasse);
        this.props.setAppSpecialite(codeSpecialite);
        this.state = {
            codeClasse : codeClasse,
            codeSpecialite : codeSpecialite,
        }
    }

    componentDidMount(){
        const {codeClasse,codeSpecialite} = this.props.match.params;
        this.props.loadEnseignements(codeClasse,codeSpecialite,this.props.tab);
    }

    static getDerivedStateFromProps(nextProps, prevState){
        const {codeClasse,codeSpecialite} = nextProps.match.params;
        const prevClasse = prevState.codeClasse;
        if(prevClasse && prevClasse !== codeClasse){
            nextProps.loadAppClasse(codeClasse);
            nextProps.loadEnseignements(codeClasse,codeSpecialite,nextProps.tab);
            return {codeClasse};
        }
        const prevSpecialite = prevState.codeSpecialite;
        if(prevSpecialite !== codeSpecialite){
            nextProps.setAppSpecialite(codeSpecialite);
            nextProps.loadEnseignements(codeClasse,codeSpecialite,nextProps.tab);
            return {codeSpecialite};
        }
        return null;
    }


    renderPage(){
        switch (this.props.tab) {
            case CODE_DEVOIRS:
                return <ListDevoirs />;
            case CODE_EXAMENS:
                return <ListExamens />;
            default:
                return <ListCours />;
        }
    }

    changeEnseignement = (page)=>{
        if(page!==this.props.tab){
            
            if(diseable(page,this.props.classe.type)){
                return;
            }
            const {codeClasse,codeSpecialite} = this.props.match.params;
            this.props.setAppPage(page);
            this.props.loadEnseignements(codeClasse,codeSpecialite,page);
            toLink(`${this.props.match.url}?p=${page}`)
        }
    }
    
    render() {
        return (
            <>
                <EnseignementMenu
                    changeEnseignement = {this.changeEnseignement}
                    page = {this.props.tab}
                    classe = {this.props.classe}
                    specialite = {this.props.specialite}/>
                {this.renderPage()}
            </>
        )
    }
}

// Exporte le composant
export default EnseignementLayout;