import React from 'react';
import {Link} from 'react-router-dom';
import classnames from 'classnames';
import { formatProgramme } from '../utils/fonctions';
import { CODE_COURS, CODE_EXAMENS, CODE_DEVOIRS } from '../routes/liens';
import { lienOuvrirChapitre } from '../navigation/cours';
import { lienOuvrirControle } from '../navigation/controles';

const images = {
    "maths" : require('../img/maths-card.png'),
    "pc" : require('../img/pc-card.png'),
    "biologie" : require('../img/biologie-card.png'),
    "examens" : require('../img/examens-card.png'),
    "devoirs" : require('../img/devoirs-card.png'),
}

class EnseignementCard extends React.Component{

    render(){
        const {title,resume,exercice,programme} = this.props;
        let link = '';
        const {type} = this.props;
        let image = null;
        if(type===CODE_COURS){
            link = lienOuvrirChapitre({chapitreID : this.props.enseignementID});
            image = images[programme.specialite.code];
        }else if(type===CODE_DEVOIRS || type===CODE_EXAMENS){
            link = lienOuvrirControle(this.props.enseignementID);
            image = images[type];
        }
        return(
            <Link style={{textDecoration:'none'}} to={link}>
                <div className="card enseignement-card">   
                    {this.props.icon && image && <img className="" src={image} alt="Card cap" />}         
                    <div className="card-body">
                        <div>
                            <div className="card-title">{title}</div>
                            <div className="clearfix">
                                {exercice && <p className="card-text float-left exercice">{exercice}</p>}
                                {programme && <p className={classnames("card-text programme",{"float-right" : exercice})}> {formatProgramme(programme)} </p>}
                            </div>
                        </div>
                        {resume && <p className="card-text resume">{resume}</p>}
                    </div>
                </div>
            </Link>
        );
    }
}

export default EnseignementCard;